import React from 'react';
import { useLocation } from 'react-router-dom';
import cls from './NavList.module.scss';
import { TypeNavLink } from 'src/types/nav';
import { NavItem } from 'src/components/UI/NavItem/NavItem';

export interface TypeNavListProps {
  navLinkArray: TypeNavLink[];
  classNameList: string;
  classNameItem?: string;
  strictHash?: boolean;
  renderProps: (val: TypeNavLink) => React.ReactNode;
}

const safeDecode = (str: string) => {
  try {
    return decodeURIComponent(str);
  } catch {
    return str;
  }
};

/**
 *        ,   .
 *     navLinkArray  NavItem (  ).
 *      renderProps.
 *
 * @param {Object} props -   NavList
 * @param {TypeNavLink[]} props.navLinkArray -    (id, name, path).
 * @param {string} props.classNameList - CSS-  <ul>.
 * @param {string} [props.classNameItem] -  CSS-   NavItem.
 * @param {boolean} [props.strictHash=false] -  true,   hash   ( ).
 * @param {(val: TypeNavLink) => React.ReactNode} props.renderProps -      .
 * @returns {JSX.Element}  <ul>   /NavItem.
 */
export const NavList: React.FC<TypeNavListProps> = ({
  navLinkArray,
  classNameList,
  classNameItem = '',
  renderProps,
  strictHash = false,
}) => {
  const location = useLocation();
  const currentFullPath = location.pathname + (strictHash ? safeDecode(location.hash) : '');

  return (
    <ul className={`${classNameList} ${cls.NavList}`}>
      {navLinkArray.map((val) => {
        const targetPath = safeDecode(val.path);
        const isActive = currentFullPath === targetPath;
        return (
          <NavItem
            key={val.id}
            to={val.path}
            active={isActive}
            className={classNameItem}
          >
            {renderProps(val)}
          </NavItem>
        );
      })}
    </ul>
  );
};

export default NavList;
