import React from 'react';
import { Link as RouterLink, LinkProps } from 'react-router-dom';
import { Link as GravityLink } from '@gravity-ui/uikit';
import cls from './NavItem.module.scss';

export interface NavItemProps extends Omit<LinkProps, 'to'> {
  to: string | { pathname?: string; hash?: string };
  active?: boolean;
  className?: string;
  style?: React.CSSProperties;
  children: React.ReactNode;
}

/**
 *      ,     .
 *   Link  react-router-dom,     (  NavItem).
 *      ,   GravityLink;     (, ),    .
 *
 * @param {Object} props -   NavItem
 * @param {string|Object} props.to -       (  ).
 * @param {boolean} [props.active] -      .
 * @param {string} [props.className] -  CSS-   <li>.
 * @param {React.CSSProperties} [props.style] - -   <li>.
 * @param {React.ReactNode} props.children -  : , , .
 *
 * @returns {JSX.Element}   <li>   .
 */
export const NavItem: React.FC<NavItemProps> = ({
  to,
  active,
  className = '',
  style,
  children,
}) => {
  const combinedClass = [cls.NavItem, className, active ? cls.active : '']
    .filter(Boolean)
    .join(' ');
  //  children     ,  GravityLink
  const isSimple =
    typeof children === 'string' ||
    (Array.isArray(children) && children.every((c) => typeof c === 'string'));

  return (
    <li
      className={combinedClass}
      style={style}
      data-active={active ? 'true' : undefined}
    >
      <RouterLink to={to}>
        {isSimple ? <GravityLink view="normal">{children}</GravityLink> : children}
      </RouterLink>
    </li>
  );
};
